import React from "react";
import { RouteComponentProps } from "react-router-dom";
import { UpdatePlacementComponent } from "../../components/UpdatePlacementComponent/UpdatePlacementComponent";

export class UpdatePlacementList extends React.Component<
  IDetailsProps,
  IDetailsState
> {
  render() {
    return (
      <UpdatePlacementComponent
        placement_id={this.props.match.params.placement_id}
      />
    );
  }
}

interface IDetailsProps extends RouteComponentProps<{ placement_id: string }> {}

interface IDetailsState {
  name: string;
}
