import React, { Fragment } from "react";
import {
  MdCheckBox,
  // MdCheckBoxOutlineBlank,
  // MdIndeterminateCheckBox,
  MdWarning,
} from "react-icons/md";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import {
  Auth,
  Basic,
  TeacherData,
  TeacherDataRole,
  FC_GetTeachersBySchool,
  FC_UpdatePlacementList,
  Placement,
  API_GetPlacementBySchool,
  FT_UpdatePlacementList,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import PlacementListPreview from "../../components/PlacementListPreview/PlacementListPreview";
import { StoreState } from "../../reducers";
import { RenderPageAccess, UsersAccess } from "../../utils/access";
import { search } from "../../utils/functions";
import validateStaff from "../../utils/validatedStaff";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
  placement: Placement;
  placement_id: string;
  FC_UpdatePlacementList: FT_UpdatePlacementList;
}

interface AppState {
  loading: boolean;
  approved_list: boolean;
  error: {
    target:
      | "requests-error"
      | "loading-update-error"
      | "main"
      | "main-success"
      | "update-error"
      | "main-error"
      | "submit-list-error"
      | "submit-month-error"
      | "submit-list-success"
      | null;
    msg: string;
  };
  data: TeacherData[];
  selected_placement: API_GetPlacementBySchool | null;
  placement_list: TeacherData[];
  roles: TeacherDataRole[];
  loading_data: boolean;
  selected_role: string;
  selected_request_id: string;
  selected_value: string;
  selected_comment: string;
  selected_auto_level: string;
  search: string;
  selected_qualification: string;
  selected_month: string;
  loading_placement_submit: boolean;
  ht_comment: string;
  selected_check_validation: string;
}

class _PlacementList extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      approved_list: false,
      selected_placement: null,
      loading: true,
      data: [],
      placement_list: [],
      roles: [],
      error: {
        target: null,
        msg: "",
      },
      loading_data: true,
      selected_role: "",
      selected_request_id: "",
      selected_value: "0",
      selected_comment: "",
      selected_auto_level: "",
      search: "",
      selected_qualification: "",
      selected_month: "",
      loading_placement_submit: false,
      ht_comment: "",
      selected_check_validation: "",
    };
  }

  submitLogin = (e: any) => {
    e.preventDefault();
  };

  componentDidMount = () => {
    if (this.props.placement.placements.length === 0)
      return <Redirect to="/placement-list-ht" />;

    RenderPageAccess(
      this.props.auth.role?.access,
      UsersAccess.HEADTEACHER_UPDATE_PLACEMENT_LIST,
      () => {
        this.startApp();
      }
    );
  };

  startApp = () => {
    if (this.props.basic.academic_calender === null) {
      this.setState({
        error: {
          target: "main-error",
          msg: "failed to load data, please reload the page (Refresh)",
        },
      });
    } else if (this.props.auth.school === null) {
      this.setState({
        error: {
          target: "main-error",
          msg: "No school assigned to you",
        },
      });
    } else {
      let school_code = this.props.auth.school.school_code;
      this.setState({
        error: {
          target: null,
          msg: "",
        },
      });
      FC_GetTeachersBySchool(school_code, (status, data, msg) => {
        if (status === true && data !== null) {
          // * get the current list
          let placementExist = this.props.placement.placements.find(
            (itm) => itm.placement_list_approval_id === this.props.placement_id
          );
          this.setState({
            selected_placement:
              placementExist === undefined ? null : placementExist,
            ht_comment:
              placementExist === undefined ? "" : placementExist?.ht_comment,
            loading_data: false,
            data: data.staff,
            roles: data.role,
            placement_list: data.staff.filter(
              (req) => validateStaff(req).validated
            ),
            // placement_list: data.staff.filter(
            //   (req) => !(req.first_name == null || req.first_name === "")
            // ),
          });
        } else {
          this.setState({
            loading_data: false,
            error: {
              msg: msg,
              target: "main-error",
            },
          });
        }
      });
    }
  };

  UpdatePlacement = (el: TeacherData, status: number) => {
    // if (status < 0) {
    //   this.setState({
    //     placement_list: [...this.state.placement_list, el],
    //   });
    // } else {
    //   this.setState({
    //     placement_list: this.state.placement_list.filter(
    //       (itm) => itm.user_id !== el.user_id
    //     ),
    //   });
    // }
  };

  submitList = (e: any) => {
    e.preventDefault();

    // if (this.state.selected_month === "")
    //   return this.setState({
    //     error: {
    //       target: "submit-month-error",
    //       msg: "Please select the month",
    //     },
    //   });

    if (
      this.props.basic.academic_calender === null ||
      this.props.auth.school === null
    )
      return this.setState({
        error: {
          target: "submit-list-error",
          msg: "You don't has a district assigned to you",
        },
      });

    // let district_code = this.props.auth.school.school_code.substring(0, 2);

    if (this.state.selected_placement === null)
      return this.setState({
        error: {
          target: "main-error",
          msg: "No placement list selected",
        },
      });

    this.setState({
      loading_placement_submit: true,
    });
    this.props.FC_UpdatePlacementList(
      this.state.selected_placement.placement_list_approval_id,
      {
        academic_year_id: this.state.selected_placement.academic_year_id,
        district_code: this.state.selected_placement.district_code,
        list: JSON.stringify(this.state.placement_list),
        month: this.state.selected_placement.month,
        school_code: this.state.selected_placement.school_code,
        ht_comment: this.state.ht_comment,
      },
      (status, msg) => {
        if (status === true) {
          this.setState({
            loading_placement_submit: false,
            error: {
              target: "submit-list-success",
              msg: `Placement list for ${this.state.selected_month} has been updated successfully`,
            },
          });
        } else {
          this.setState({
            loading_placement_submit: false,
            error: {
              target: "submit-list-error",
              msg: msg,
            },
          });
        }

        setTimeout(() => {
          this.setState({
            error: {
              msg: "",
              target: null,
            },
          });
        }, 3000);
      }
    );
    console.log("janvier");
  };

  setApprovedList = () =>
    this.setState({ approved_list: !this.state.approved_list });

  SelectAll = () => this.setState({ placement_list: this.state.data });
  DeSelectAll = () => this.setState({ placement_list: [] });

  render() {
    let RequestsList =
      this.state.selected_role === ""
        ? this.state.data
        : this.state.data.filter(
            (itm) => itm.role_id === this.state.selected_role
          );

    RequestsList =
      this.state.selected_qualification === ""
        ? RequestsList
        : RequestsList.filter(
            (itm) => itm.qualification === this.state.selected_qualification
          );

    RequestsList = search(RequestsList, this.state.search, {
      email: true,
      first_name: true,
      full_name: true,
      last_name: true,
      phone_numbers: true,
      qualification: true,
      role: true,
      role_id: true,
      status: true,
      user_id: true,
      username: true,
    });

    if (this.state.loading_data === true)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    if (this.state.error.target === "main-error") {
      return (
        <Alert theme="danger" fixed>
          {this.state.error.msg}
        </Alert>
      );
    }

    if (this.state.approved_list === true)
      return (
        <>
          <PlacementListPreview
            placement_list={this.state.placement_list}
            header={
              <>
                <div className="flex">
                  <div className="flex-1">
                    <div className="text-xl">
                      <h2>
                        School code:{" "}
                        <b>{this.state.selected_placement?.school_code}</b>
                      </h2>
                      <h2>
                        School name:{" "}
                        <b>{this.state.selected_placement?.school_name}</b>
                      </h2>
                      <h2>
                        Done by:{" "}
                        <b>
                          {this.state.selected_placement?.head_teacher_name}
                        </b>
                      </h2>
                      <h2>
                        Month: <b>{this.state.selected_placement?.month}</b>
                      </h2>
                      <h2>
                        District:{" "}
                        <b>{this.state.selected_placement?.district_name}</b>
                      </h2>
                    </div>
                  </div>
                  <div className="">
                    <Button
                      size="lg"
                      theme="danger-light"
                      onClick={this.setApprovedList}
                      disabled={this.state.loading_placement_submit}
                      type="button"
                    >
                      Close
                    </Button>
                  </div>
                </div>

                <h1 className="text-center text-3xl font-bold mb-4">
                  Placement list
                </h1>
              </>
            }
            footer={
              <>
                <form onSubmit={this.submitList}>
                  <div className="mt-5 flex border-t pt-3">
                    <div className="flex-1"></div>
                    <div className="flex-1">
                      {this.state.error.target === "submit-list-success" && (
                        <Alert theme="success">{this.state.error.msg}</Alert>
                      )}
                      {this.state.error.target === "submit-list-error" && (
                        <Alert theme="danger">{this.state.error.msg}</Alert>
                      )}
                      <div className="">
                        <span>Comment:</span>
                        <textarea
                          value={this.state.ht_comment}
                          // onChange={(e) =>
                          //   this.setState({
                          //     ht_comment: e.target.value,
                          //   })
                          // }
                          readOnly
                          className="w-full h-32 border-2   rounded p-2"
                        ></textarea>
                      </div>
                      <div className="text-right">
                        <Button
                          theme="success"
                          size="lg"
                          className="flex-1 animate__animated animate__pulse animate__infinite"
                          loading={this.state.loading_placement_submit}
                        >
                          Update placement list
                        </Button>
                      </div>
                    </div>
                  </div>
                </form>
              </>
            }
          />
        </>
      );

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.error.target === "submit-list-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}
        {this.state.error.target === "submit-list-error" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        <div className="fixed bottom-0 right-0 flex left-0 bg-white shadow-lg border-t-2 px-3 items-center">
          <div className="main-side-spacer"></div>
          <div className="flex-1 p-2 flex items-center">
            <div className="mr-4 bg-green-50 text-green-700 px-2">
              <span className="text-sm">Total selected</span>
              <h3 className="font-bold text-2xl -mt-1">
                {this.state.placement_list.length}
              </h3>
            </div>
            <div className="px-2 bg-gray-50 rounded">
              <span className="text-sm">Total Teachers</span>
              <h3 className="font-bold text-2xl -mt-1">
                {this.state.data.length}
              </h3>
            </div>
            <div className="flex-1"></div>
            <div className="animate__animated animate__zoomIn animate__faster flex">
              {this.state.placement_list.length >= 1 && (
                <Button
                  theme="accent-light"
                  onClick={this.setApprovedList}
                  className=" animate__faster"
                >
                  Preview list
                </Button>
              )}

              <form onSubmit={this.submitList}>
                <Button
                  theme="success"
                  className="animate__animated animate__pulse animate__infinite ml-1"
                  loading={this.state.loading_placement_submit}
                >
                  Update placement list
                </Button>
              </form>
            </div>
          </div>
        </div>

        <DashboardContainer>
          {this.state.error.target === "main" && (
            <Alert theme="danger">{this.state.error.msg}</Alert>
          )}
          {this.state.error.target === "loading-update-error" && (
            <Alert theme="danger">{this.state.error.msg}</Alert>
          )}

          <div className="flex items-center">
            <div className="flex-1">
              <Heading>
                Update placement list {this.state.selected_placement?.month}{" "}
              </Heading>
              <p className="opacity-60 text-lg -mt-1 font-bold text-accent-900 dark:text-accent-800">
                {this.props.auth.school?.school_name}
              </p>
            </div>
          </div>

          <div className="mt-3">
            <div className="border border-t mt-2 mb-1 flex items-center pl-3 pr-2 rounded">
              <div className="flex-1 pr-2">
                <TextInput
                  error=""
                  label="Search"
                  name=""
                  type="search"
                  onChange={(e) => this.setState({ search: e.target.value })}
                  placeholder="Search by teacher name"
                  value={this.state.search}
                  size="sm"
                />
              </div>
              <div className="">
                <InputSelect
                  data={[
                    { title: "All Roles", value: "" },
                    ...this.state.roles.map((el) => ({
                      title: el.role,
                      value: el.role_id,
                    })),
                  ]}
                  error=""
                  name="select_role"
                  onChange={(e) =>
                    this.setState({ selected_role: e.target.value })
                  }
                  label="Role"
                  placeholder="Select role"
                  value={this.state.selected_role}
                  size="sm"
                />
              </div>
              {this.props.basic.qualifications !== null && (
                <div className="px-2">
                  <InputSelect
                    data={[
                      { title: "All", value: "" },
                      ...this.props.basic.qualifications.map((el) => ({
                        title: el.qualification_name,
                        value: el.qualification_name,
                      })),
                    ]}
                    error=""
                    name="select_qualification"
                    onChange={(e) =>
                      this.setState({ selected_qualification: e.target.value })
                    }
                    label="Qualification"
                    placeholder="Select Qualification"
                    value={this.state.selected_qualification}
                    size="sm"
                  />
                </div>
              )}
              {/* <div className="p-2 px-3 ml-3 bg-accent-800 bg-opacity-30 rounded">
                <label className="">Results:</label>
                <h4 className="text-2xl -mt-1 font-bold">
                  {RequestsList.length}
                </h4>
              </div> */}
            </div>
            <div className="p-2 flex">
              {/* {this.state.placement_list.length !== this.state.data.length && (
                <Button
                  theme="default"
                  size="sm"
                  className="flex items-center"
                  onClick={this.SelectAll.bind(this)}
                >
                  <MdIndeterminateCheckBox className="inline-block text-lg mr-1" />
                  <span>Select all</span>
                </Button>
              )} */}

              {/* {this.state.placement_list.length !== 0 && (
                <Button
                  theme="default"
                  size="sm"
                  className="flex items-center ml-2"
                  onClick={this.DeSelectAll.bind(this)}
                >
                  <MdIndeterminateCheckBox className="inline-block text-lg mr-1" />
                  <span>De-select all</span>
                </Button>
              )} */}
            </div>
            <table className="w-full text-left table-auto border">
              <thead>
                <tr>
                  <th></th>
                  <th>Names</th>
                  <th>First name</th>
                  <th>Last name</th>
                  <th>Phone number</th>
                  <th>Role</th>
                  <th>Qualification</th>
                </tr>
              </thead>
              <tbody>
                {RequestsList.length <= 0 ? (
                  <tr>
                    <td
                      colSpan={8}
                      className="p-8 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      <Heading size="md" className="opacity-60 font-light">
                        No results found
                      </Heading>
                    </td>
                  </tr>
                ) : (
                  RequestsList.map((req, i) => {
                    let existOnPlacement = this.state.placement_list.findIndex(
                      (itm) => itm.user_id === req.user_id
                    );

                    let validatedCheck = validateStaff(req);

                    return (
                      <Fragment key={i}>
                        <tr
                          className={`group border rounded hover:bg-blue-500 hover:bg-opacity-20 ${
                            existOnPlacement < 0
                              ? ""
                              : "bg-green-100 bg-opacity-10"
                          } `}
                        >
                          <td
                            className="p-1 border cursor-pointer"
                            key={i}
                            onClick={this.UpdatePlacement.bind(
                              this,
                              req,
                              existOnPlacement
                            )}
                          >
                            {existOnPlacement < 0 ? (
                              <button
                                onClick={() =>
                                  this.setState({
                                    selected_check_validation: req.user_id,
                                  })
                                }
                              >
                                <MdWarning
                                  className="text-3xl text-yellow-500 mx-auto"
                                  title="This teacher is not validated"
                                />
                              </button>
                            ) : (
                              <MdCheckBox className="text-3xl text-green-600 mx-auto" />
                            )}
                          </td>
                          <td className="p-2 border">{req.full_name}</td>
                          <td className="p-2 border">{req.first_name}</td>
                          <td className="p-2 border">{req.last_name}</td>
                          <td className="p-2 border">{req.phone_numbers}</td>
                          <td className="p-2 border">{req.role}</td>
                          <td className="p-2 border">{req.qualification}</td>
                        </tr>
                        {this.state.selected_check_validation ===
                          req.user_id && (
                          <tr>
                            <td colSpan={10} className="pb-5">
                              <div className="pb-2 rounded-b-lg -mt-1 animate__animated animate__zoomIn animate__faster border bg-red-100">
                                <div className="p-1 px-2 flex bg-red-200">
                                  <h2 className="flex-1 text-lg text-red-800 font-bold">
                                    <span className="p-0.5 text-center rounded-full bg-red-600 pr-1 inline-block text-white">
                                      {validatedCheck.errors.length}
                                    </span>{" "}
                                    Items to be validated
                                  </h2>
                                  <Button
                                    className=""
                                    size="sm"
                                    theme="danger-light"
                                    onClick={() =>
                                      this.setState({
                                        selected_check_validation: "",
                                      })
                                    }
                                  >
                                    Close
                                  </Button>
                                </div>

                                <div className="px-2 gap-1 rounded grid md:grid-cols-2 lg:grid-cols-3 pt-2">
                                  {validatedCheck.errors.map((itm, i) => (
                                    <div
                                      key={i}
                                      className="border rounded full mb-1 mx-2 border-red-300 bg-white p-1 flex"
                                    >
                                      <MdWarning className="pr-1 text-xl inline-block text-yellow-600" />{" "}
                                      <p>{itm}</p>
                                    </div>
                                  ))}
                                </div>
                              </div>
                            </td>
                          </tr>
                        )}
                      </Fragment>
                    );
                  })
                )}
              </tbody>
            </table>
          </div>
        </DashboardContainer>
        <div className="h-24"></div>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  placement,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  placement: Placement;
} => {
  return {
    auth: auth,
    basic: basic,
    placement: placement,
  };
};

export const UpdatePlacementComponent = connect(mapStateToProps, {
  FC_UpdatePlacementList,
})(_PlacementList);
